import React, {Component} from 'react';
import {connect} from 'react-redux';
import {setTitle} from '../../store/actionCreators';
import {Carousel, NoticeBar, WhiteSpace, WingBlank} from 'antd-mobile';
import './About.sass';

const mapAction2Props = (dispatch, props) => {
    return {
        ...props,
        setTitle: (...args) => dispatch(setTitle(...args)),
    };
};

class About extends Component {
    constructor(props) {
        super(props);
        this.state = {
            drawerOpen: false
        };
    }

    componentDidMount() {
        this.props.setTitle('About');
    }

    render() {
        return (
            <div className="about">
                <NoticeBar marqueeProps={{loop: true,}}>http://www.monolith-iot.com</NoticeBar>
                <WhiteSpace/>
                <WingBlank>
                    <Carousel
                        autoplay={true}
                        infinite
                        className="headerCarousel"
                    >
                        <div className="carouselItem" style={{backgroundColor: '#FF9808'}}>
                            <div className="title">
                                
                            </div>
                        </div>
                        <div className="carouselItem" style={{backgroundColor: '#3171FA'}}>
                            <div className="title">
                                MonolithIoT
                            </div>
                        </div>
                    </Carousel>
                </WingBlank>
                <WingBlank className="article">
                    <h1 className="title"></h1>
                    <div className="text">
                        <p>
                            (),WMS,ERPSAP
                        </p>
                        <p>
                            /DWS,(
                            )DHL
                        </p>
                        <p>
                            24,1PLM
                        </p>
                    </div>
                </WingBlank>
            </div>
        );
    }
}

export default connect(null, mapAction2Props)(About);
